.header {
  transition: 0.3s linear;
  transform: translate(10px);
  display: flex;
  height: 50px;
}

.footer {
  transition: 0.3s linear;
  transform: translate(10px);
  display: flex;
  height: 50px;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
}

.class {
  border: 1px solid blue;
  font-size: 20px;
}